
@import "./rem.scss";

$screen-xs:                   0;
$screen-sm:                   400px;
$screen-md:                   600px;
$screen-lg:                   800px;
$screen-xl:                   1000px;
$screen-xxl:                  1800px;

@mixin column {
  display: flex;
  flex-direction: column;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Segoe UI", "Helvetica Neue", Arial, sans-serif;
}

.content {
  @include column;
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;
}

.toolbars {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 0 15px;

  &__block  {
    flex-basis: calc(100% / 4 - 15px);

    @media (max-width: rem($screen-sm)) {
      flex-basis: 100%;
      margin-bottom: 10px;
    }

    @media (min-width: rem($screen-sm)) and (max-width: rem($screen-xl)) {
      flex-basis: calc(100% / 3 - 15px);
      margin-bottom: 10px;
    }
  }

  &__block select {
    width: 100%;
    padding: .3em .5em;
    height: 2.3em;
    background-color: #fff;
    border: 1px solid #706f6f;
    border-radius: 6px;
  }

  &__block input {
    width: calc(100% - 40px);
    font-size: 14px;
    height: 2.3em;
    padding-left: 5px;
    vertical-align: middle;
    border: 1px solid #706f6f;
    border-radius: 6px;
  }
}

.m {
  @media (min-width: rem($screen-sm)) and (max-width: rem($screen-xl)) {
    flex-basis: calc(100% - 15px);
  }
}
.search-button {
  display: block;
  float: right;
  margin-left: 6px;
  width: 32px;
  height: 32px;
  line-height: normal;
  background: #fff url(https://www.imumk.ru/Content/skins/bubble/images/icons/icon-search-25.png) center no-repeat;
  border: 0;
  border-radius: 4px;
}

.courses-list {
  display: flex;
  flex-wrap: wrap;

  &__item {
    margin-left: 15px;
    margin-right: 15px;
    margin-bottom: 45px;
    flex-grow: 1;
    min-width: 170px;
    max-width: 170px;
    border: solid 1px #b5b5b5;
    border-radius: 10px;
    overflow: hidden;

    @media (max-width: rem($screen-sm)) {
      max-width: 100%;
    }

    @media (min-width: rem($screen-sm)) and (max-width: rem($screen-md)) {
      max-width: calc(100% / 2 - 30px);
    }

    @media (min-width: rem($screen-md)) and (max-width: rem($screen-lg)) {
      max-width: calc(100% / 3 - 30px);
    }

    @media (min-width: rem($screen-lg)) and (max-width: rem($screen-xl)) {
      max-width: calc(100% / 4 - 30px);
    }

  }
}

.course {
  @include column;
  padding-left: 16px;
  padding-right: 16px;
  padding-bottom: 10px;
  color: #3c3c3b;

  &__image-wrap {
    position: relative;
    padding-top: 138px;
    overflow: hidden;
    margin-left: -16px;
    margin-right: -16px;
    margin-bottom: 10px;
  }

  &__image {
    position: absolute;
    top: -50%;
    left: -50%;
    right: -50%;
    bottom: -50%;
    width: 100%;
    height: auto;
    margin: auto;
  }

  &__title {
    font-size: 14px;
  }

  &__grade {
    font-size: 14px;
    margin-bottom: 5px;
  }

  &__genre {
    font-size: 11px;
    margin-bottom: 10px;
  }

  &__price {
    font-size: 12px;
    color: #fff;
    border: 0;
    border-radius: 6px;
    background: #00c1ff;
    padding-top: 5px;
    padding-bottom: 5px;
  }
}
